import './Customize_Screen.css';
import React, { useState, useEffect } from 'react';
import { ReactComponent as Timer } from '../svg/icon-timer.svg';
import Circle from './Circle';

const getInitialPositions = (playerBoxes, colors) => {
    return playerBoxes.map((_, index) => ({
      x: 10 + ((index % 12) * 80),
      y: 10 + (Math.floor(index / 12) * 80),
      color: colors[index % colors.length],
    }));
  };



const Customize_Screen = ({ screenCount, playerBoxes = [], positions = [], colors =[], onSavePositions, onDanceFormation, onShowPopup, onSceneTimes, onMovingTimes}) => {
    const initial_position = getInitialPositions(playerBoxes, colors);
    const [sceneTimes, setSceneTimes] = useState([]);
    const [movingTimes, setMovingTimes] = useState([]);
    const [realbetweenTimes, setRealBetweenTimes] = useState([]);
    const [realmovingTimes, setRealMovingTimes] = useState([]);
    const [editingIndex, setEditingIndex] = useState(null);
    const [selectedScene, setSelectedScene] = useState(1); // 선택된 scene
    const [storedPositions, setStoredPositions] = useState(positions); // 각 scene의 포지션을 저장하는 상태
    const [currentPositions, setCurrentPositions] = useState(positions); // 현재 scene의 포지션을 저장하는 상태
    const [nextPositions, setNextPositions] = useState(positions); // 다음 scene의 포지션을 저장하는 상태
    let screenindex = 1;

    // Update state when screenCount changes
    useEffect(() => {
        setSceneTimes((prevSceneTimes) => {
            const updatedSceneTimes = [...prevSceneTimes];
            while (updatedSceneTimes.length < screenCount) {
                updatedSceneTimes.push(0); // default value
            }
            return updatedSceneTimes.slice(0, screenCount); // trim excess if screenCount decreases
        });

        setMovingTimes((prevMovingTimes) => {
            const updatedMovingTimes = [...prevMovingTimes];
            while (updatedMovingTimes.length < screenCount - 1) {
                updatedMovingTimes.push(0); // default value
            }
            return updatedMovingTimes.slice(0, screenCount - 1); // trim excess if screenCount decreases
        });

    }, [screenCount]);


    // Update stored positions when positions change
    useEffect(() => {
        if (selectedScene !== null && positions.length > 0) {
            setStoredPositions((prevStoredPositions) => {
                const updatedStoredPositions = [...prevStoredPositions];

                // 현재 선택된 scene에 대한 위치 업데이트
                updatedStoredPositions[selectedScene - 1] = positions;
                onSavePositions(updatedStoredPositions[selectedScene - 1]); // 변경된 위치 저장
                return updatedStoredPositions;
            });
        }
    }, [positions]);

    useEffect(() => {
        if (storedPositions.length > 0 && !onShowPopup) {
            onDanceFormation(storedPositions); // 부모 컴포넌트로 데이터 전달
        }
    }, [storedPositions, onDanceFormation]);




    const handleSceneClick = (sceneIndex) => {
        if (selectedScene !== sceneIndex) {
            // Save current scene positions
            setStoredPositions((prevStoredPositions) => {
                const updatedStoredPositions = [...prevStoredPositions];
                if (selectedScene !== null && updatedStoredPositions[selectedScene - 1]) {
                    updatedStoredPositions[selectedScene - 1] = positions;
                }
                return updatedStoredPositions;
            });

            setSelectedScene(sceneIndex); // Switch to the new scene
            // Load positions for the selected scene
            const selectedPositions = storedPositions[sceneIndex - 1] || initial_position; // Default to initial positions if none exist
            onSavePositions(selectedPositions); // Load saved positions for the selected scene
            setCurrentPositions(storedPositions[sceneIndex - 1]);
            setNextPositions(storedPositions[sceneIndex]);
        }
    };


    const Circle_minimap = ({ playerBoxes, positions}) => {
        return (
            <div className='MiniMap_Container'>
                <div className='MiniMap'>
                    {playerBoxes.map((box, index) => (
                        <Circle
                            key={index}
                            width="12px"
                            height="12px"
                            color={box.color}
                            opacity={1}
                            position="absolute"
                            x = {positions[index]?.x / 5 || 0}
                            y = {positions[index]?.y / 5 || 0}
                        />
                    ))}
                </div>
            </div>
        )
    };
    


    const handleSceneTimeChange = (index, e) => {
        const newTimes = [...sceneTimes];
        const updatedMovingTimes = [...realmovingTimes];
        
        // 사용자 입력을 받아 newTimes 업데이트
        const newValue = e.target.value;
        newTimes[index] = newValue;
        
        // 입력된 값에 1000을 곱해 updatedMovingTimes 업데이트
        updatedMovingTimes[index] = newValue * 1000;
    
        // 업데이트된 값을 상태에 반영
        setSceneTimes(newTimes);
        onSceneTimes(updatedMovingTimes); // 적절한 콜백 호출
        setRealMovingTimes(updatedMovingTimes);
    };
    
    const handleMovingTimeChange = (index, e) => {
        const newTimes = [...movingTimes];
        const updatedBetweenTimes = [...realbetweenTimes];
    
        // 사용자 입력을 받아 newTimes 업데이트
        const newValue = e.target.value;
        newTimes[index] = newValue;
    
        // 입력된 값에 1000을 곱해 updatedBetweenTimes 업데이트
        updatedBetweenTimes[index] = newValue * 1000;
    
        // 업데이트된 값을 상태에 반영
        setMovingTimes(newTimes);
        onMovingTimes(updatedBetweenTimes); // 적절한 콜백 호출
        setRealBetweenTimes(updatedBetweenTimes);
    };
    

    const handleDoubleClick = (type, index) => {
        setEditingIndex(`${type}-${index}`);
    };

    const handleBlur = () => {
        setEditingIndex(null);

    };

    const elements = [];

    for (let i = 0; i < screenCount; i++) {
        const formattedNumber = String(i + 1).padStart(3, '0');
        const isSelected = selectedScene === i + 1;
        const currentPositions = storedPositions[i] || initial_position// 처음엔 기본값 사용

        elements.push(
            <div key={`scene-${i}`} className={`Customize_Down_Menu_scene ${isSelected ? 'selected' : ''}`} onClick={() => handleSceneClick(i + 1)}>
                <span className='Customize_Down_Menu_scene_top_box'>
                    <span className='Customize_Down_Menu_scene_time'>
                        {editingIndex === `scene-${i}` ? (
                            <input
                                type="number"
                                value={sceneTimes[i]}
                                onChange={(e) => handleSceneTimeChange(i, e)}
                                className="Customize_Down_Menu_scene_time_input"
                                onBlur={handleBlur}
                                autoFocus
                            />
                        ) : (
                            <span
                                onDoubleClick={() => handleDoubleClick('scene', i)}
                                className="Customize_Down_Menu_scene_time_input"
                            >
                                {sceneTimes[i]}
                            </span>
                        )}
                        <span className='Customize_Down_Menu_scene_time_unit'>sec</span>
                    </span>
                    <span className='Customize_Down_Menu_scene_number'>#{formattedNumber}</span>
                </span>

                {/* 처음엔 storedPositions, 클릭 후 dynamicPositions으로 미니맵 표시 */}
                <span className='Customize_Down_Menu_scene_block'>
                    <Circle_minimap playerBoxes={playerBoxes} positions={isSelected ? positions : currentPositions} />
                </span>
            </div>
        );

        // Add moving time box if it's not the last iteration
        if (i < screenCount - 1) {
            elements.push(
                <div key={`moving-time-${i}`} className='Customize_Down_Menu_moving_time_box'>
                    <Timer />
                    {editingIndex === `moving-${i}` ? (
                        <input
                            type="number"
                            value={movingTimes[i]}
                            onChange={(e) => handleMovingTimeChange(i, e)}
                            className="Customize_Down_Menu_moving_time_input black"
                            onBlur={handleBlur}
                            autoFocus
                        />
                    ) : (
                        <span
                            onDoubleClick={() => handleDoubleClick('moving', i)}
                            className="Customize_Down_Menu_moving_time_input white"
                        >
                            {movingTimes[i]}
                        </span>
                    )}
                    <span className='Customize_Down_Menu_moving_time_unit'>sec</span>
                </div>
            );
        }
    }

    return (
        <div className='Customize_Down_Menu'>
            {elements}
        </div>
    );
};

export default Customize_Screen;
