import React, { useState, useEffect } from 'react';
import Circle from './Drag_Circle';


function WebSocketComponent() {
  const [socket, setSocket] = useState(null);
  const [players, setPlayers] = useState([null, null, null, null, null]);
  const [opacities, setOpacities] = useState([1, 1, 1, 1, 1]);


  useEffect(() => {
    const newSocket = new WebSocket('ws://localhost:8765');
    setSocket(newSocket);

    newSocket.onopen = () => {
      console.log('WebSocket 연결 성공');
    };

    newSocket.onmessage = (event) => {
      const { player1: newPlayer1, player2: newPlayer2, player3: newPlayer3, player4: newPlayer4, player5: newPlayer5 } = JSON.parse(event.data);
      const newPlayers = [newPlayer1, newPlayer2, newPlayer3, newPlayer4, newPlayer5];
      
      setPlayers(newPlayers);
      setOpacities(newPlayers.map(player => player ? 1 : 0));

      console.log('CLEINT: 서버로부터 메시지 수신:', newPlayer1, newPlayer2, newPlayer3, newPlayer4, newPlayer5);
    };

    newSocket.onerror = (error) => {
      console.error('WebSocket 오류:', error);
    };

    newSocket.onclose = () => {
      console.log('WebSocket 연결 종료');
    };

    return () => {
      newSocket.close();
    };
  }, []); // useEffect를 한 번만 실행하기 위해 빈 배열을 전달합니다.

  return (
    <div>
    {players.map((player, index) => {
      if (player) {
        console.log(`CLIENT: Rendering player ${index + 1}:`, player, `with opacity ${opacities[index]}`);
        return (
          <Circle
            width = '60px'
            height = '60px'
            color={index === 0 ? 'red' : index === 1 ? 'green' : index === 2 ? 'blue' : index === 3 ? 'yellow' : index === 4 ? 'cyan' : index}
            x={player.x * (11/8)}
            y={player.y * (11/8)}
            opacity={opacities[index]}
          />
        );
      }
      return null;
    })}
  </div>
  );
}

export default WebSocketComponent;
