import React, { useState } from 'react';


function Circle({ width, height, color, x, y, opacity = 1, position = 'absolute' }) {
    return (
        <div
            style={{
                width: width,
                height: height,
                backgroundColor: color,
                borderRadius: '50%',
                position: position,
                top: y,
                left: x,
                opacity: opacity,
            }}
        ></div>
    );
}

export default Circle;