import React, { useState, useEffect } from 'react';
import rgbaWithOpacity from './rgbaWithOpacity';
import Square from './Square';



function RenderBoard1(rows, cols, customize = false, opacity = 0.3) {
    const squares = [];
    for (let rowNum = 0; rowNum < rows; rowNum++) {
      const row = [];
      for (let colNum = 0; colNum < cols; colNum++) {
        let color;
        if (!customize) {
          if ((rowNum < 2 || rowNum > rows - 1) || (colNum < 2 || colNum > cols - 3)) { // 2번째부터 마지막-1 행, 2번째부터 마지막-1 열
            const originalColor = (rowNum + colNum) % 2 === 0 ? '#ffffff' : '#000000'; // 원래 색상 결정
            color = rgbaWithOpacity(originalColor, opacity); // 원래 색상에 지정된 투명도를 적용한 rgba 값
          } else {
            color = (rowNum + colNum) % 2 === 0 ? 'white' : 'black';
          }
        } else {
          color = (rowNum + colNum) % 2 === 0 ? `rgba(255, 255, 255, ${opacity})` : `rgba(0, 0, 0, ${opacity})`;
        }
        const size = customize ? '20px' : '80px'; 
        row.push(<Square key={`${rowNum}-${colNum}`} color={color} width={size} height ={size}/>);
      }
      squares.push(<div key={rowNum} className="Map-row">{row}</div>);
    }
    return squares;
  }

export default RenderBoard1;