import React, { useState } from 'react';

function Drag_Circle({ width, height, color, x, y, opacity = 1, position = 'absolute', onPositionChange }) {
    const [isDragging, setIsDragging] = useState(false);
    // const BoardOffset = { x: 658, y: 134 }; // Customize-Map-board의 좌표 오프셋
    const [dragOffset, setDragOffset] = useState({ x: 0, y: 0 });


    const calculateBoardOffset = () => {
        const offsetX = window.innerWidth * 0.35; // 35% of the viewport width
        const offsetY = window.innerHeight * 0.07; // 7% of the viewport height
        return { x: offsetX, y: offsetY };
    };
    
    // 초기값 설정
    let BoardOffset = calculateBoardOffset();
    
    // 화면 크기가 변경될 때 동적으로 업데이트 (예: 창 크기 조정 시)
    window.addEventListener('resize', () => {
        BoardOffset = calculateBoardOffset();
    });
    const handleDragStart = (e) => {
        setIsDragging(true);
        setDragOffset({
            x: e.clientX - e.target.getBoundingClientRect().left - 10,
            y: e.clientY - e.target.getBoundingClientRect().top + 80,
        });
    };

    const handleDragEnd = (e) => {
        setIsDragging(false);

        // Get the bounding rectangle of the board to check if drag is outside
        const boardRect = document.querySelector('.Customize-Map-board').getBoundingClientRect();
        const newX = Math.round((e.clientX - BoardOffset.x - dragOffset.x) / 10) * 10;
        const newY = Math.round((e.clientY - BoardOffset.y - dragOffset.y) / 10) * 10;
        
        

        // Check if dragged outside the board
        if (e.clientX < boardRect.left + 15 || e.clientX > boardRect.right - 15 || e.clientY < boardRect.top + 15 || e.clientY > boardRect.bottom - 15) {
            console.log('Player dragged outside the board. Stopping drag.');
            return;
        }

        // If inside the board, update the position
        onPositionChange(newX, newY); // 부모 컴포넌트에서 위치를 업데이트할 수 있도록 콜백 호출
    };

    return (
        <div
            style={{
                width: width,
                height: height,
                backgroundColor: color,
                borderRadius: '50%',
                position: position,
                top: y,
                left: x,
                opacity: opacity,
                cursor: 'grab',
            }}
            draggable
            onDragStart={handleDragStart}
            onDragEnd={handleDragEnd}
        ></div>
    );
}

export default Drag_Circle;
