import React, {useState} from 'react';
import Circle from './Drag_Circle';
import './Player.css'; // Assuming you have a CSS file for styles

const Player = ({ color, number, isClicked, onClick}) => {
  const [isHovered, setIsHovered] = useState(false);
  const [isEditing, setIsEditing] = useState(false);
  const [name, setName] = useState(`Dancer ${number}`);


  const backgroundColor = isClicked
    ? 'rgb(125, 137, 248)'
    : isHovered
    ? 'rgba(120, 130, 140, 0.5)'
    : 'transparent';

  const handleDoubleClick = () => {
    setIsEditing(true);
  };

  const handleNameChange = (e) => {
    setName(e.target.value);
  };

  const handleBlur = () => {
    setIsEditing(false);
  };

  return (
    <div
      className="Customize_Side_Menu_Player"
      style={{ backgroundColor }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      onClick={onClick}
    >
        <Circle
            width="32px"
            height="32px"
            color={color}
            x={0}
            y={0}
            opacity={1}
            position="relative"
        />
        {isEditing ? (
            <input
            type="text"
            value={name}
            onChange={handleNameChange}
            onBlur={handleBlur}
            onKeyPress={(e) => {
                if (e.key === 'Enter') handleBlur();
            }}
            autoFocus
            className="Customize_Side_Menu_Player_input"
            />
        ) : (
            <span
            className="Customize_Side_Menu_Player_text"
            onDoubleClick={handleDoubleClick}
            >
            {name}
            </span>
        )}
    </div>
  );
};

export default Player;
