import React, {useEffect, useState} from 'react';
import { ReactComponent as Logo } from '../svg/logo.svg';
import { ReactComponent as New} from '../svg/icon_new.svg';
import { ReactComponent as Copy} from '../svg/icon_copy.svg';
import { ReactComponent as Back} from '../svg/icon_back.svg';
import { ReactComponent as Front} from '../svg/icon_front.svg';
import { ReactComponent as Down} from '../svg/icon_down.svg';
import { ReactComponent as Play} from '../svg/icon_onplay.svg';
import { Link } from 'react-router-dom';

import SocialLinks from './SocialLinks'; 
import './Banner.css'; // css 파일

function Banner({onUndo, onRedo, onPlay, onName, L_position, isUndoDisabled, isRedoDisabled, playerBoxes}) {
    const [Title, setTitle] = useState("New Choreography");
    const [isEditing, setIsEditing] = useState(false);

    const handleDoubleClick = () => {
        setIsEditing(true);
    }

    const handleNameChange = (e) => {
        setTitle(e.target.value);
        onName(e.target.value);
    }

    const handleBlur = () => {
        setIsEditing(false);
    }

    const handleUndoClick = () => {
        console.log('Undo button clicked');
        onUndo(); // 부모로부터 전달된 onUndo 함수 실행
    };

    // Front 버튼 클릭 시 실행되는 함수
    const handleRedoClick = () => {
        console.log('Redo button clicked');
        onRedo(); // 부모로부터 전달된 onRedo 함수 실행
    };

    const generateRandomString = (length = 40) => {
        const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        let result = '';
        for (let i = 0; i < length; i++) {
          result += characters.charAt(Math.floor(Math.random() * characters.length));
        }
        return result;
      };

    const handleNewClick = () => {
        const randomString = generateRandomString();
        const newUrl = `${window.location.origin}/customize/${randomString}`; // Append the random string to the current page's path

        // Open a new page with the generated URL
        window.open(newUrl, '_blank');
    
        // Copy the new URL to the clipboard
        navigator.clipboard.writeText(newUrl).then(
            () => {
                alert('URL has been copied to the clipboard!'); // Optional: Show a message to the user
            },
            (err) => {
                console.error('Failed to copy: ', err);
            }
        );
    };

    const handleCopyClick = () => {
        const currentUrl = window.location.href; // Get the current page URL
      
        // Copy the current URL to the clipboard
        navigator.clipboard.writeText(currentUrl).then(
          () => {
            alert('Current page URL has been copied to the clipboard!'); // Optional: Show a message to the user
          },
          (err) => {
            console.error('Failed to copy: ', err);
          }
        );
      };


      

  return (

    <header className="Banner">
        <div className='icon-container'>
        {/* <Link to="/"> */}
            <span className="icon-box">
                <Logo className='Customize-logo'/>
            </span> 
        {/* </Link> */}
            <span className="icon-box" onClick={handleNewClick}>
                <New className='Customize-logo'/>
            </span> 
            <span className="icon-box" onClick={handleCopyClick} >
                <Copy className='Customize-logo'/>
            </span> 
            <span className="icon-box" onClick={onPlay}>
                <Play className='Customize-logo'/>
            </span>
            {/* <span className="icon-box" onClick={isUndoDisabled? undefined : handleUndoClick} style= {{opacity: isUndoDisabled ? 0.4 : 1, pointerEvents: isUndoDisabled ? 'none' : 'auto'}}>
                <Back alt="Back" className='Customize-logo' />
            </span> 
            <span className="icon-box" onClick={isRedoDisabled? undefined : handleRedoClick} style= {{opacity: isRedoDisabled ? 0.4 : 1, pointerEvents: isRedoDisabled ? 'none' : 'auto'}}>
                <Front alt="Front" className='Customize-logo' />
            </span>  */}

            <span className = "Banner-title-box">
                <span className='Banner-title-text-box'> 
                    {isEditing ? (
                        <input
                            type="text"
                            value={Title}
                            onChange={handleNameChange}
                            onBlur={handleBlur}
                            onKeyPress={(e) => {
                                if (e.key === 'Enter') handleBlur();
                            }}
                            autoFocus
                            className="Banner-title-text black"
                        />
                    ) : (
                        <span
                            className="Banner-title-text"
                            onDoubleClick={handleDoubleClick}
                        >
                            {Title}
                        </span>
                    )}
                </span>
                {/* <Down className='down-logo'/> */}
            </span>
        </div>

        {/* <SocialLinks position={L_position} /> */}
    </header>

  );
}

export default Banner;