import React, { useState, useEffect, useRef } from 'react';
import Circle from './Circle';

function Screen({ Positions = [], MakeupTime = [], BetweenTime = [], HandleScreen = 'Stop' }) {
    const [ScreenPositions, setScreenPositions] = useState([]);
    const [count, setCount] = useState(0);
    const [PauseUnActive, setPauseUnActive] = useState(false);
    
    // useRef를 사용하여 애니메이션 및 타이머 ID를 관리
    const animationFrameId = useRef(null);
    const timeoutId = useRef(null);
    const pausedProgress = useRef(0);
    const isPaused = useRef(false);
    const startTime = useRef(0);

    useEffect(() => {
        if (HandleScreen === 'Play' && Positions.length > 0) {
            console.log('Play Screen');
            if (count === 0) {
                // 첫 화면을 표시
                setScreenPositions(Positions[0]);
            }
            startScreen();
        } else if (HandleScreen === 'Pause') {
            if (!PauseUnActive) {
                console.log('Pause Screen');
                cancelAnimationFrame(animationFrameId.current);
                clearTimeout(timeoutId.current);
                isPaused.current = true; // Pause 상태 저장
                pausedProgress.current = performance.now() - startTime.current; // 경과 시간 저장
            }
        } else if (HandleScreen === 'Stop') {
            console.log('Stop Screen');
            cancelAnimationFrame(animationFrameId.current);
            clearTimeout(timeoutId.current);
            setScreenPositions([]); // 화면 초기화
            setCount(0); // count 초기화
            pausedProgress.current = 0; // 진행 상태 초기화
            isPaused.current = false; // Pause 상태 해제
            setPauseUnActive(false); // Pause 버튼 활성화
        }
    }, [HandleScreen, Positions]);

    const startScreen = () => {
        console.log('Start Screen');
        if (count < Positions.length) {
            showScreen(count);
        }
    };

    const animatePositionChange = (startPositions, endPositions, duration) => {
        console.log('Animating position change:', startPositions, endPositions, duration);
        startTime.current = performance.now() - pausedProgress.current; // 경과된 시간 고려
        pausedProgress.current = 0; // Resume 시 경과 시간 초기화

        const animate = (currentTime) => {
            const elapsed = currentTime - startTime.current;
            const progress = Math.min(elapsed / duration, 1);

            const newPositions = startPositions.map((startPosition, index) => {
                const endPosition = endPositions[index];
                const currentX = startPosition.x + (endPosition.x - startPosition.x) * progress;
                const currentY = startPosition.y + (endPosition.y - startPosition.y) * progress;
                return { x: currentX, y: currentY, color: endPosition.color };
            });

            setScreenPositions(newPositions);

            if (progress < 1 && HandleScreen === 'Play') {
                animationFrameId.current = requestAnimationFrame(animate);
            }
        };

        animationFrameId.current = requestAnimationFrame(animate);
    };

    const showScreen = (index) => {
        console.log('-----------------------------------');
        console.log('Show Screen:', index);
        console.log('Positions:', Positions[index]);
        console.log('MakeupTime:', MakeupTime[index]);
        console.log('BetweenTime:', BetweenTime[index]);

        if (index < Positions.length && HandleScreen === 'Play') {
            console.log('Showing screen:', index);

            if (index > 0) {
                animatePositionChange(Positions[index - 1], Positions[index], MakeupTime[index]);

                if (index === Positions.length - 1) {
                    setPauseUnActive(true);
                }
            }

            timeoutId.current = setTimeout(() => {
                setCount(index + 1); // 다음 화면으로 넘어갈 준비
                showScreen(index + 1); // 재귀 호출로 다음 화면 표시
            }, MakeupTime[index] + BetweenTime[index]);
        }
    };
    
    return (
        <div>
            {Array.isArray(ScreenPositions) ? (
                ScreenPositions.map((position, index) => (
                    <Circle
                        key={index}
                        width="82.5px"
                        height="82.5px"
                        color={position.color}
                        x={position.x * (11 / 8)}
                        y={position.y * (11 / 8)}
                        opacity={1}
                        position="absolute"
                    />
                ))
            ) : (
                <div>No positions to display</div> // Optional: Fallback for empty state
            )}
        </div>
    );
    
}    

export default Screen;
