import React, { useState, useEffect } from 'react';
import Drag_Circle from "./Drag_Circle";

function CustomizeBoardCircle({ Player_array = [], positions = [], updatePosition }) {

    return (
        <div>
            {Player_array.map((player, index) => (
                <Drag_Circle
                    key={index}
                    x={positions[index]?.x || player.position.x}
                    y={positions[index]?.y || player.position.y}
                    width="60px"
                    height="60px"
                    color={player.color}
                    opacity={1}
                    onPositionChange={(newX, newY) => updatePosition(index, newX, newY)}
                />
            ))}
        </div>
    );
}

export default CustomizeBoardCircle;
